<template>
    <div class="shortcut-menu-con">
        <div v-for="(item,index) in lists" :key="index" class="shortcut-menu-cell">
            <div class="shortcut-menu-content" @click="clickMenu(item)">
                <div class="shortcut-menu-imgicon" :style="getImageStyle(item)"></div>
                <span class="shortcut-menu-title">{{item.menuName}}</span>
            </div>
        </div>
    </div>
</template>
<script>
    /**
     * @Description: 快捷菜单组件
     * @Props: [data: Array] 数据列表
     */

    export default {
        name: 'ShortcutMenu',
        props: {
            data: {
                default: []
            }
        },
        data () {
            return {
                lists: [],
            }
        },
        mounted () {
            this.updateData(this.data);
        },
        methods: {
            //点击菜单
            clickMenu (item) {
            },
            //获取图标地址
            getImageStyle (item) {
                var icon = item.icon || 'prod-center';
                var imgurl = require(`@/assets/images/menuicon/${icon}.png`);
                return {
                    backgroundImage: `url(${imgurl})`
                }
            },
            //更新数据
            updateData (val) {
                this.lists = val || [];
            }
        },
        watch: {
            data (val) {
                this.updateData(val);
            }
        }
    }
</script>
<style lang="less" scoped>
</style>


